}
.Nav__userInfo{
    font-family: 'Inter', sans-serif;
    font-size: 22px;
    font-weight: 400;
    color: #00FFFA;
    margin: 0;
}
.nav_menu{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    gap: 30px;
}