.quiz{
    /*border: 1px solid #555;*/
    overflow: hidden;
    /*border-radius: 5px;*/
    cursor: pointer;
    transition: all .2s ease-in-out;
    position: relative;

    padding: 20px;
    border-radius: 15px;
    border: 2px solid #80cbff;
    background: rgb(0 255 250 / 5%);
    box-shadow: 0px -4px 4px rgb(0 255 250 / 20%), 0px 4px 4px rgb(0 255 250 / 20%), inset 0px 0px 6px rgb(0 255 250 / 80%);
}
.quiz:hover{
    transform: translate(-5px, -5px);
}
.quiz__details{
    /*padding: 30px;*/
    text-align: center;
}
.quiz__image_box{
    width: 100%;
    height: 180px;
    background-color: #555;
    overflow: hidden;
}
.quiz__image{
    width: 100%;
}
.quiz__name{
    font-family: 'Roboto Slab', Serif;
    color: #fff;
    margin: 0;
    letter-spacing: 1px;
    font-size: 24px;
    text-align: left;
}
.quiz__info{
    display: flex;
    justify-content: space-between;
}
.quiz__info-text{
    font-family: 'Roboto', Serif;
    font-size: 16px;
    color: #fff;
    font-weight: 400;
    margin: 20px 0;
}
.quiz__getStarted_button{
    display: inline-block;
    text-align: center;
    text-decoration: none;
}
.quiz__getStarted_button:hover{
    color: #fff;
}
.quizEditButton{
    position: absolute;
    top: 10px;
    right: 10px;
}